import React from 'react'
import {Box, Paper, TextField, Typography} from '@material-ui/core'
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import Button from 'Components/LoadingButton'
import * as yup from 'yup'
import {makeStyles} from '@material-ui/core/styles'
import {DateTimePicker} from '@material-ui/pickers'
import GamesAvailable from './GamesAvailable';
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import { toastSuccess, toastInfo } from 'Components/Toast';

const styles = makeStyles(theme => ({
  form: {
    padding: theme.spacing(1),
    '& > *': {
      marginBottom: theme.spacing(1)
    }
  }
}))


const mutation = graphql`
  mutation MegaBasicDetailsMutation($data: MegladronInput!) {
    createMegladron(data: $data) {
      megladron {
        _id
        title
        description
      }
    }
  }
`

const schema = yup.object().shape({
  title: yup.string().required().max(20),
  description: yup.string().required(),
  maxPlayers: yup.number().positive().integer().required().min(50),
  image: yup.string().required()
});

let startTime, endTime;

function MegaBasicDetails(props) {

  const classes = styles()

  const { register, handleSubmit, errors } = useForm({
    resolver: yupResolver(schema)
  });

  function onSubmit(data) {
    //console.log(data)
    let game = document.querySelector("[name=game]").value
    //console.log(Date(endTime))
    if(endTime === null || startTime === null || game === null) {
      return toastInfo("End Date, Start Date, Game values are not present or they are still default one");
    }
    let addedData = {...data, game, endTime: new Date(endTime).getTime().toString(), startTime: new Date(startTime).getTime().toString()}
    console.log(addedData)
    CommitRelay({data: addedData}, mutation, (response, error) => {
      console.log(response, error)
      if(!error) {
        toastSuccess("Megladron created")
      }
    })
  }
  console.log("Form submitted", errors)
  return (
    <Box p={1}>
      <Paper component="form" onSubmit={handleSubmit(onSubmit)} className={classes.form}>
        <Typography component="h2" variant="h5" color="textSecondary">Megladron details</Typography>
        <TextField inputRef={register} name="title" label={"Title"} fullWidth error={errors.title !== undefined} helperText={errors.title?.message} required/>
        <TextField inputRef={register} name="description" label={"Description"} multiline={true} rows={5} required helperText={errors.title?.message} error={errors.description !== undefined} fullWidth/>
        <TextField inputRef={register} name="maxPlayers" label={"Max Players"} required helperText={errors.maxPlayers?.message} error={errors.maxPlayers !== undefined} fullWidth/>
        <TextField inputRef={register} name="image" label={"Image URL"} required helperText={errors.image?.message} error={errors.image !== undefined} fullWidth/>
        <GamesAvailable />
        <DateTimePicker inputRef={register}  name="startTime" label="Event Start Time" disablePast fullWidth onChange={e => {startTime = e}}/>
        <DateTimePicker inputRef={register} name="endTime" label="Event End Time" disablePast fullWidth onChange={e => {endTime=e}} />
        <Button type="submit">Submit</Button>
      </Paper>
    </Box>
  )
}

export default MegaBasicDetails