import React from 'react'
import Form from 'Components/Form'
import { makeStyles } from '@material-ui/core/styles'
import { TextField, Chip } from '@material-ui/core'
import Button from 'Components/LoadingButton'
import CustomFields from 'Components/CustomFields'
import Header from 'Components/Header'
import graphql from 'babel-plugin-relay/macro'
import CommitMutation from 'Components/CommitRelay'
import { toastSuccess } from 'Components/Toast'


const styles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(1)
  }
}))

const mutation = graphql`

mutation AddGameMutation($data: GameInput!){
  addGame(gameFields: $data){
		game {
      title
    }
  }
}

`


function AddGame(){

  const classes = styles()
  let [loading, setLoading] = React.useState(false)
  let tableData = []
  function setTableData(table){
    tableData = table.data
  }

  function onSubmit(data) {
    console.log(data)
    setLoading(true)
    const obj = {...data, 
      customFields: tableData.map(item => ({label: item.label, type: item.type, options: item.options.split(",").map(i => i.trim())}))
    }
    CommitMutation({data: obj}, mutation, (response, errors) => {
      //console.log(response)
      setLoading(false)
      if(!errors){
        toastSuccess("Game added successfully")
      }
    })
  }

  return (
    <div className={classes.root}>
      <Form method="post" url="/api/games" onSubmit={onSubmit}>
        <TextField label={'Game title'} fullWidth name={'title'} />
        <TextField label={'Description'} name="description" multiline={true} rows={5} fullWidth />
        <TextField label={'Image URL'} name={"image"} fullWidth  />
        <TextField label={'Metrics'} name={'metrics'} fullWidth helperText={"Things on which player will be judged or given points"} />
        <CustomFields columns={[
            { title: 'Heading', field: 'label'},
            {
              title: 'Field Type',
              field: 'type',
              lookup: { select: 'Dropdown', multipleSelect: 'Multiple Select Dropdown', checkbox: "Checkbox", number: "Number", text: "Text Field"},
            },
            {
              title: "Parameters", 
              field: "options",
              render: rowData => {
                if(rowData.options !== undefined)
                  return rowData.options.split(",").map((item, index) => <Chip label={item} key={index} style={{margin: 4}}/>)
              }
            }
          ]}
          getTableData={(data) => setTableData(data)}
        />
        <Button type="submit" fullWidth isLoading={loading}>Add Game</Button>
      </Form>
    </div>
  )
}

export default AddGame