import {toastError} from 'Components/Toast'

export default function(errors){
  //console.log("Errors", errors, errors.response)
  try{
    if(errors.length > 0){
      showError(errors)
    }else if(errors.message !== undefined) {
      toastError(errors.message)
    }
  }catch(err){
    console.log(errors, err)
    toastError("Unknown error occured")
  }
}

function showError(errors){
  console.log(errors)
  if(errors === undefined){
    return toastError("Unknow error occured")
  }
  for(let i = 0; i < errors.length; i++){
    
    if(errors[i].message !== undefined )toastError(errors[i].message)
  }
}