import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import QueryRenderer from 'Components/QueryRenderer'
import CustomList from 'Components/CustomList'

function makeDate(text){
  //2020-02-24T09:19:13.741Z
  let a = new Date(+text).toString()
  //console.log(a)
  return  a.substr(4, 11)+" at "+a.substr(16, 5)
}

const query = graphql`
query TournamentsSectionQuery{
  tournament(pagination: {offset: 0, limit: 30}){
    _id
    title
    totalRegistered
    time
    gameId {
      title
    }
  }
}
` 

function TournamentsSection(props){
  return <QueryRenderer query={query} component={data => <TournamentsSectionComponent {...data} selectTournament={props.selectTournament}/>} />
}

function TournamentsSectionComponent(props) {
  let [selected, setSelected] = React.useState("")
  let items = props.tournament.map(item => ({
    id: item._id, 
    primaryText: item.gameId.title+" "+item.title,
    secondaryText: makeDate(item.time)
  }))

  function itemClick(item) {
    setSelected(item.id)
    props.selectTournament(item)
  }

  return <CustomList items={items} button divider heading={"Select a tournament to send notification"} selected={[selected]} itemClick={itemClick}/>
}

export default TournamentsSection