import React from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'

const styles = makeStyles(theme => ({
  form: {
    background: theme.palette.background.paper,
    padding: theme.spacing(1),
    position: 'relative',
    borderRadius: 4,
    overflow: 'hidden',
    boxShadow: theme.shadows[1],
    '& > *':{
      marginBottom: theme.spacing(1)
    }
  },
}))

function Form(props) {

  const classes = styles()

  function onSubmit(e){
    e.preventDefault()
    if(props.submitTriggered){
      props.submitTriggered(e)
    }
    let el = e.target
    let inputs = el.querySelectorAll("[name]")
    let data = {}
    let formData = new FormData()
    for(let i = 0; i < inputs.length; i++){
      data[inputs[i].getAttribute("name")] = inputs[i].value
      formData.append(inputs[i].getAttribute("name"), inputs[i].value)
    }
    let filesEl = document.querySelectorAll("[type=file]");
    //console.log(file)
    for(let i = 0; i < filesEl.length; i++){
      for(let k = 0; k < filesEl[i].files.length; k++){
        formData.append(filesEl[i].getAttribute("name"), filesEl[i].files[k], filesEl[i].files[k].name)
      }
      
    }
    if(props.onSubmit){
      return props.onSubmit(data, formData, el)
    }
  }


  return (
    <form className={props.className || classes.form} onSubmit={onSubmit} style={props.style}>
      <Typography color="textSecondary" variant="h5">{props.heading}</Typography>
      {props.children}
    </form>
  )

}

Form.propTypes = {
  onSubmit: PropTypes.func,
  children: PropTypes.node.isRequired,
  submitTriggered: PropTypes.func,
  success: PropTypes.func,
  error: PropTypes.func,
  url: PropTypes.string,
  method: PropTypes.string,
  resetOnSubmit: PropTypes.bool,
  successMessage: PropTypes.string,
  addButton: PropTypes.bool,
  heading: PropTypes.string
}

export default Form;