import React from 'react'
import CheckBox from '@material-ui/core/Checkbox'

function Check({label, ...props}) {

  // let [checked, setChecked] = React.useState(props.defaultValue === "checked" || props.value === "checked")

  // console.log("Cleicked")

  return (
    <div>
      <CheckBox onChange={props.onChange} {...props}/>
      {label}
    </div>
  )
}

export default Check;