import React from 'react'
import PropTypes from 'prop-types'
import TextField from './TextField'
import Select from './Select'
import MultipleSelect from './MultipleSelect'
import Checkbox from './Checkbox'
import { Typography } from '@material-ui/core'
import {makeStyles} from '@material-ui/core/styles'

const styles = makeStyles(theme => ({
  'root': {
    '& > *' : {
      marginBottom: theme.spacing(1)
    }
  }
}))

//textfield {name, type, label, value}
//select {name, type, label, options}
//multipleSelect {name, type, label, options}

function FormBuilder(props) {

  const classes = styles()

  function MapItems({item}) {
    switch(item.type){
      case 'checkboxGroup':
        return <div>
          <Typography variant="h4">{item.title}</Typography>
          {
            item.items.map((checkbox, index) => <Checkbox key={index} name={item.title+"_"+checkbox} onChange={props.onChange} {...checkbox}/>)
          }
        </div>
      case 'checkbox':
        return <Checkbox onChange={props.onChange} {...item}/>
      case 'select':
        return <Select onChange={props.onChange} {...item}/>
      case 'multipleSelect':
        return <MultipleSelect onChange={props.onChange} {...item}/>
      default:
        return <TextField onChange={props.onChange} {...item}/>
    }
  }

  return (
    <div className={classes.root} id={props.id}>
      {
        props.fields.map((item, index) => {
          return <MapItems key={item.name} item={item}/>
        })
      }  
    </div>
  )
}

FormBuilder.defaultProps = {
  onChange: () => {},
  fields: []
}

FormBuilder.propTypes = {
  onChange: PropTypes.func,
  fields: PropTypes.array.isRequired
}

export default FormBuilder