import React from 'react'
import { Grid } from '@material-ui/core'
import { useHistory } from 'react-router-dom'
import View from './View/Tournaments'
import Header from 'Components/Header'
import Add from '@material-ui/icons/Add'
import QueryRenderer from 'Components/QueryRenderer'
import graphql from 'babel-plugin-relay/macro'
import Fab from 'Components/Fab'

const query = graphql`
  query ViewTournamentsQuery{
    tournament(past: "a", pagination: {offset: 0, limit: 30}){
      ...Tournaments_tournament
    }
  }
`

function ViewTournaments(props){
  const history = useHistory()
  return (
    <React.Fragment>
      <Grid container>
        <Grid item xs={12}>   
          <QueryRenderer 
          query={query}
          component={data => <View {...data} />}/>
        </Grid>
        <Fab onClick={() => history.push('/tournaments/add')}/>
      </Grid>
    </React.Fragment>
  )
}

export default ViewTournaments