import { initialState } from './index'

export default function AddTournamentReducer(state = initialState.AddTournament, action)  {
  switch(action.type){
    case 'ADD_TOURNAMENT::SET_GAME':
      return ({
        ...state,
        game: action.game
      })

    case 'ADD_TOURNAMENT::SET_CUSTOM_FIELDS':
      return ({
        ...state,
        customFields: {
          ...state.customFields,
          [action.name]: action.value
        }
      })

    case 'ADD_TOURNAMENT::SET_PRIZES':
      return ({
        ...state,
        prizes: action.data
      })
    case 'ADD_TOURNAMENT::ADD_GAME_DATA':
      return ({
        ...state,
        customFields: [
          ...action.data
        ]
      })
    
    case "ADD_TOURNAMENT::SET_METRICS":
      return ({
        ...state,
        metrics: [
          ...action.metrics
        ]
      })

    default: 
      return state
  }
}