import {
  Environment,
  Network,
  RecordSource,
  Store,
  QueryResponseCache
} from 'relay-runtime';
import Auth from 'Store/actions/AuthService';
import ErrorUtils from './ErrorUtils'

const oneMinute = 60 * 1000;

//Because rarely a data would change in less than an hour
const cache = new QueryResponseCache({ size: 250, ttl: oneMinute * 60});

function fetchQuery(
  operation,
  variables,
  cacheConfig
) {

  const queryID = operation.text;
  const isMutation = operation.operationKind === 'mutation';
  const isQuery = operation.operationKind === 'query';
  const forceFetch = cacheConfig && cacheConfig.force;

  // Try to get data from cache on queries
  const fromCache = cache.get(queryID, variables);
  if (
    isQuery &&
    fromCache !== null &&
    !forceFetch
  ) {
    return fromCache;
  }

  let extraHeaders = {}

  if(Auth.isUserLoggedIn()){
    const data = Auth.getData()
    extraHeaders = {
      "authorization": "Bearer "+data.accessToken,
      "refreshToken": data.refreshToken
    }
  }
  let env = "Local"
  try {
    env = document.querySelector("[name=environment]").value
  }catch(err) {
    env = "Local"
  }
  let url = ""
  if(env === "Production") {
    url = "https://www.gamingstars.in/graphql"
  }else if(env === 'Testing'){
    url = "https://gaming-stars.herokuapp.com/graphql"
  }else{
    url = "http://localhost:5000/graphql"
  }
  console.log("Will be sending request to ", url)
  return  fetch(url, {
      method: 'POST',
      headers: {
        // Add authentication and other headers here
        'content-type': 'application/json',
        ...extraHeaders
      },
      body: JSON.stringify({
        query: operation.text, // GraphQL text from input
        variables,
      }),
    }).then(response => {
      return response.json();
    }).then(json => {
            // Update cache on queries
      if (isQuery && json) {
        cache.set(queryID, variables, json);
      }
            // Clear cache on mutations
      if (isMutation) {
        cache.clear();
      }
      if(json.errors){
        ErrorUtils(json.errors)
      }
      return json;
    });
}

const environment = new Environment({
  network: Network.create(fetchQuery),
  store: new Store(new RecordSource()),
});

export default environment;

