import React from 'react'
import { withRouter } from 'react-router-dom'
import MaterialTable from 'Components/MaterialTable'
import TextField from '@material-ui/core/TextField'
import CustomList from 'Components/CustomList'
import QueryRenderer from 'Components/QueryRenderer'
import graphql from 'babel-plugin-relay/macro'
import GoBackAppBar from 'Components/GoBackAppbar'
import { toastInfo, toastSuccess } from 'Components/Toast'
import LoadingButton from 'Components/LoadingButton'
import {makeStyles} from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import CommitRelay from 'Components/CommitRelay'

const UPDATE_NAME = "updateResults"

const styles = makeStyles(theme => ({
  container: {
    padding: theme.spacing(1),
    '& > *' :{
      marginBottom: theme.spacing(1)
    }
  }
}))

function calculateDynamicPoint(total, position){
  //console.log(Math.floor(total/position))
  return Math.floor(total/position)
}

function RenderSuggestiveField({tableData, names}) {
  let [list, setList] = React.useState([])
  //console.log(names)
  function onChange(val){
    tableData.onChange(val)
    if(val === ""){
      return setList([])
    }
    let a = names.filter(name => name.gamingUsername.indexOf(val) !== -1).splice(0, 5)
    setList(a)
  }

  function itemClick(item) {
    tableData.onChange(item.primaryText)
    setList([])
  }

  let items = list.map((item, index) => ({primaryText: item.gamingUsername, id: index}))

  return (<div style={{position: "relative"}}>
    <TextField onChange={e => onChange(e.target.value)} name={tableData.columnDef.field} placeholder={tableData.columnDef.title} fullWidth value={tableData.value || ""}/>
    { list.length > 0 &&  <CustomList items={items} button dense divider itemClick={itemClick} style={{position: "absolute", width: "100%"}}/>}
  </div>)
}

function setItem(data){
  localStorage.setItem(UPDATE_NAME, JSON.stringify(data))
}

function getItem(){
  let a = localStorage.getItem(UPDATE_NAME)
  if(a === null){
    return []
  }
  try{
    return JSON.parse(a)
  }catch(err){
    console.log(err)
    return []
  }
}

function UpdateResultsComponent({specificTournament, ...props}){
  let {tournament} = specificTournament
  let [tableData, setTableData] = React.useState({names: tournament.registered, data: getItem()})
  function handleData(data, modified, action, modifiedEdit){
    let names = []

    if(action === "ADD"){
      names = tableData.names.filter(name => name.gamingUsername !== modified.username)
    }else if(action === "EDIT") {
      toastInfo("Dynamic editing of name is still experimental. Might cause issue sometimes")
      names = [...tableData.names.filter(name => name.gamingUsername !== modified.username), ...tournament.registered.filter(name => name.gamingUsername === modifiedEdit.username)]
    }else {
      names = [...tableData.names, ...tournament.registered.filter(name => name.gamingUsername === modified.username)]
    }
    setItem(data)
    setTableData({data, names})
  }

  let columns = tournament.metricsPoints.map(item => ({title: item.label, field: item.label, type: "numeric"}))
  columns.unshift({
    title: "Name",
    field: "username",
    editComponent: data => <RenderSuggestiveField tableData={data} names={tableData.names}/>
  })
  columns.push({
    title: "Group",
    field: "group",
    type: "numeric"
  })
  columns.push({
    title: "Points",
    field: "points",
    type: "numeric"
  })
  columns.push({
    title: "Credit",
    type: "currency",
    field: "credit"
  })

  function calculatePoints(){
    if(tableData.data.length === 0){
      toastInfo("Add data in table to calculate")
    }
    let a = []
    let metrics = {}
    //push metrics table in key value pair
    for(let i = 0; i < tournament.metricsPoints.length; i++){
      metrics[tournament.metricsPoints[i].label] = {points: tournament.metricsPoints[i].points, dynamic: tournament.metricsPoints[i].dynamic}
    }
    let skip = ["username", "group", "points", "tableData", "credit"]
    for(let i = 0; i< tableData.data.length; i++) {
      let points = 0;
      for(let key in tableData.data[i]){
        if(skip.indexOf(key) === -1){
          points += metrics[key].dynamic ? calculateDynamicPoint(+metrics[key].points, +tableData.data[i][key]) : +metrics[key].points
        }
      }
      a.push({
        ...tableData.data[i],
        points
      })
    }
    //console.log(a)
    setTableData({data: a, names: tableData.names})
  }


  return (
    <div>
      <MaterialTable 
        title={'Update results'} 
        columns = {columns}
        tableData={tableData.data} 
        getTableData={handleData}
      />
      <Button fullWidth onClick={calculatePoints}>Calculate points | Run once to get overview. If everything is correct</Button>
    </div>
  )
}

const query = graphql`
  query UpdateResultsQuery ($id: ID!){
    specificTournament(id: $id){
      tournament{
        _id
        registered {
          gamingUsername
        }
        metricsPoints {
          label
          points
          dynamic
        }
      }
    }
  }
`


const mutation = graphql`
  mutation UpdateResultsMutation($data: UpdateResultsInput!) {
    updateResults(data: $data) {
      tournament {
        _id
        resultsUpdated
      }
      message
    }
  }
`

function transformData(data){
  let nonMetrics = ["points", "username", "group", "tableData", "credit"]
  return data.map(item => {
    let metrics = []
    for(let key in  item) {
      if(nonMetrics.indexOf(key) === -1){
        metrics.push({
          label: key,
          value: +item[key]
        })
      }
    }
    return ({
      metrics,
      gamingUsername: item.username,
      credit: +item.credit,
      groupPosition: +item.group || -1
    })
  })
  // console.log(data)
  // return data
}

function UpdateResults(props) {
  const classes = styles()
  let [submitting, setSubmitting] = React.useState(false)

  function submitData(){
    let tableData = getItem()
    if(tableData.length === 0){
      return toastInfo("Table is empty unable to submit")
    }
    if(!window.confirm("Do you wish to submit results. It can't be updated again")) {
      return
    }
    setSubmitting(true)
    let data = {id: props.match.params.tournamentId, results: transformData(tableData)}
    //console.log(data)
    CommitRelay({data}, mutation, (response, errors) => {
      console.log(response, errors)
      setSubmitting(false)
      if(!errors){
        toastSuccess(response.updateResults.message)
      }
    })
  }

  function clearData(){
    if(!window.confirm("Do you wish to clear all data in local storage")){
      return 
    }
    localStorage.removeItem(UPDATE_NAME)
    window.location.reload()
    //toastInfo("Refresh page now.")
  }

  return (<div>
    <GoBackAppBar title={'Update Results'}/>
    <div className={classes.container}>
      <Button onClick={clearData}>Clear table data</Button>
      <QueryRenderer query={query} variables={{id: props.match.params.tournamentId}} component={data => <UpdateResultsComponent {...data} />}/>
      <LoadingButton isLoading={submitting} onClick={submitData}>Update Results</LoadingButton>
    </div>
  </div>)
}

export default withRouter(UpdateResults)