import { initialState } from './index'

const TOGGLE_MODAL = "MODAL::TOGGLE"
const TOGGLE_DRAWER = "DRAWER::TOGGLE"

const SnackbarReducer = (state = initialState.Independent, action) => {
  switch(action.type) {
    case TOGGLE_DRAWER: 
      return {
        ...state,
        drawerOpen: action.state
      }
    case TOGGLE_MODAL:
      return {
        ...state,
        modalOpen: !state.modalOpen
      }
    case "SETTINGS::ENVIRONMENT": 
      return {
        environment: action.env
      }
    default:
      return state

  }
}

export default SnackbarReducer;