import Toast from 'izitoast'

export const toastWarning = (message, others) => Toast.warn({
  position: "topRight",
  color: "yellow",
  message,
  ...others,
})

export const toastSuccess = (message, others) => Toast.success({
  position: "topRight",
  color: "green",
  message,
  ...others,
})
export const toastError = (message, others) => Toast.error({
  position: "topRight",
  color: "red",
  message,
  ...others,
})
export const toastInfo = (message, others) => Toast.info({
  position: "topRight",
  color: "blue",
  message,
  ...others
})