import React from 'react'
import GoBackAppbar from 'Components/GoBackAppbar'
import QueryRenderer from 'Components/QueryRenderer'
import graphql from 'babel-plugin-relay/macro'
import TournamentCard from './Comps/TournamentComp'
import { Grid } from '@material-ui/core'
import { makeStyles }  from '@material-ui/core/styles'

const styles = makeStyles(theme => ({
  root: {
    width: "100%",
    margin: 0
  },
}))

const query = graphql`
  query AccountTournamentsQuery {
    user {
      registeredTournaments {
        _id
        title
        time
        entryFee
        gameId {
          title
        }
      }
    }
  }
`

function AccountTournaments(props) {
  return (
    <div>
      <GoBackAppbar title={'Tournaments'} />
      <QueryRenderer query={query} component={data => <AccountTournamentsComponent {...data}/>}/>
    </div>
  )
}

function AccountTournamentsComponent({user, ...props}) {
  const classes = styles()
  return <Grid container spacing={2} className={classes.root}>
    {
      user.registeredTournaments.map(item => <Grid item xs={12} key={item._id}>
        <TournamentCard {...item}/>
      </Grid>)
    }
  </Grid>
}

export default AccountTournaments