import React from 'react'
import {SwipeableDrawer, useMediaQuery, Divider, Typography, Box} from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import CustomList from 'Components/CustomList'
import { useHistory, withRouter } from 'react-router-dom'
import {DrawerState} from './AppBar'
import AuthService from 'Store/actions/AuthService'
import Person from '@material-ui/icons/PersonOutline'

let navigation = [
  {
    id: 0,
    primaryText: "Home",
    to: "/home"
  },
  {
    id: 1,
    primaryText: "Tournaments",
    to: "/tournaments"
  },
  {
    id: 23,
    primaryText: "Mega",
    to: "/mega"
  },
  {
    id: 2,
    primaryText: "Games",
    to: "/games"
  },
  {
    id: 4,
    primaryText: "Wallet",
    to: "/wallet"
  },
  {
    id: 5,
    primaryText: "Notification",
    to: "/notification"
  },
  {
    id: 300,
    primaryText: "Account",
    to: "/account"
  }
  
]

const drawerWidth = 240

const styles = makeStyles(theme => ({
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    backgroundColor: "#1B2430",
    color: "#fff"
  },
  toolbar: {
    ...theme.mixins.toolbar,
    alignItems: "center",
    justifyContent: "center",
    display: "flex",
    backgroundColor: "rgb(35, 47, 62)"
  },
  title: {
    color: "#fff"
  },
  image: {
    width: 32,
    display: "inline-block",
    marginRight: theme.spacing(1)
  },
  activeClass: {
    backgroundColor: "#000 !important"
  },
  hello: {
    padding: theme.spacing(1),
    alignItems: "center",
    justifyContent: "center",
    display: "flex",
    backgroundColor: "rgb(35, 47, 62)"
  },
  sideNav: {
    display: "flex",
    width: "100%",
    justifyContent: 'space-between',
    flexDirection: "column",
    height: "100%"
  }
}))

function findActiveIndex(items){
  let activeIndex = -1
  items.forEach((item, index) => {
    
    if(item.to === "/")
      return
    if(window.location.pathname.indexOf(item.to) !== -1){
      activeIndex = item.id
    }
  })
  return activeIndex;
}


function SideNav(props){
  const classes = styles()
  const matches = useMediaQuery('(min-width:960px)')
  let history = useHistory()
  let drawer = DrawerState.useContainer()

  function itemClick(item, index) {
    history.push(process.env.REACT_APP_BASE_URL+""+item.to)
    drawer.setDrawer(false)
  }

  let data = AuthService.getData()

  return (
    <SwipeableDrawer
      className={classes.drawer}
      open={drawer.drawerOpen}
      onClose={() => drawer.setDrawer(false)}
      onOpen={() => drawer.setDrawer(true)}
      variant={matches ? "permanent" : "temporary"}
      classes={{
        paper: classes.drawerPaper,
      }}
      anchor="left"
      >
      <div className={classes.toolbar}>
        <img src="/admin/logo192.png" className={classes.image} />
        <Typography className={classes.title}>Gaming Stars</Typography>
      </div>
      <Box p={1} className={classes.sideNav}>
        <CustomList 
          items={navigation} 
          button 
          listClass={' '} 
          itemClick={itemClick} 
          selected={[findActiveIndex(navigation)]}
          activeClass={classes.activeClass}/>
        <Typography className={classes.hello}> <Person /> &nbsp;&nbsp; {data.username || "Jon Doe"} </Typography>
      </Box>
    </SwipeableDrawer>
  )
}


export default withRouter(SideNav)