import React from 'react'
import CustomList from 'Components/CustomList'
import graphql from 'babel-plugin-relay/macro'
import QueryRenderer from 'Components/QueryRenderer'
import RequestComponent from './RequestComponent'

const query = graphql`
query AllWalletRequestsQuery($pagination: MongoosePagination) {
  getWithdrawRequests(pagination: $pagination){
    ...RequestComponent_getWithdrawRequests
  }
}
`


function AllWalletRequests(props) {
  //console.log(props, "new one")
  return (
    <div>
      <QueryRenderer query={query} variables={{pagination: {page: 1, limit: 30}}} component={data => <RequestComponent {...data}/>} />
    </div>
  )
}

export default AllWalletRequests;