import React from 'react'
import AppBar from './AppBar'
import SideNav from './SideNav'
import {DrawerState} from './AppBar'

function Drawer(props){
  return (<React.Fragment>
      <DrawerState.Provider>
        <AppBar />    
        <SideNav />
      </DrawerState.Provider>
    </React.Fragment>
  )
}

export default Drawer