import React from 'react'
import MegaBasicDetails from './MegaBasicDetails'
import ViewMegladron from './ViewMegladron'
import {Switch, Route} from 'react-router-dom'
import UpdateMega from './UpdateMega/UpdateMega'

function Mega(props) {
  return (
    <Switch>
      <Route path={'/mega/view/:id'} component={UpdateMega} />
      <Route path={'/mega/add'} component={MegaBasicDetails}/>
      <Route path={'/mega'} component={ViewMegladron} />
    </Switch>
  )
}

export default Mega;