import React from 'react'
import Fab from '@material-ui/lab/SpeedDial'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction';
import SpeedDialIcon from '@material-ui/lab/SpeedDialIcon';
import { makeStyles } from '@material-ui/core/styles'
import EventAvailable from '@material-ui/icons/EventAvailableOutlined'
import {useHistory, withRouter} from 'react-router-dom'


const styles = makeStyles(theme => ({
  speedDial: {
    position: "fixed",
    zIndex: 99,
    bottom: theme.spacing(4),
    right: theme.spacing(4)
  }
}))

const actions = [
  {icon: <EventAvailable />, text: "Add a round", to: "addRound"}
]

function SpeedDial(props){
  let [open, setOpen] = React.useState(false)
  const classes = styles()
  let history = useHistory()

  function handleOpen(){
    setOpen(true)
  }

  function handleClose() {
    setOpen(false)
  }

  function itemClick(item) {
    //console.log()
    history.push(`${props.match.url}/${item.to}`)
  }

  return (<Fab 
    open={open}
    className={classes.speedDial}
    onClose={handleClose}
    onOpen={handleOpen}
    ariaLabel="Add mega event description"
    icon={<SpeedDialIcon />}
    direction={"up"}
  >
    {
      actions.map((item, index) => <SpeedDialAction icon={item.icon} tooltipTitle={item.text} key={index} onClick={() => itemClick(item)}/>)
    }
  </Fab>)
}

export default withRouter(SpeedDial)