import React from 'react'
import {withRouter} from 'react-router-dom'
import QueryRenderer from 'Components/QueryRenderer'
import graphql from 'babel-plugin-relay/macro'
import Header from 'Components/Header'
import ModifyTournamentRender from './ModifyTournamentRender'

const query = graphql`
query ModifyQuery($id: ID!){
  specificTournament(id: $id) {
    tournament {
      _id
        title
        totalRegistered
        registered{
          gamingUsername
        }
        entryFee
        maxPlayers
        time
        image
        prizes{
          position
          prize
        }
        customFields {
          label
          value
        }
        credentials{
          label
          value
        }
    }
  }
}
`

function ModifyTournament(props){

  //console.log(props.match.params.tournamentId)

  return (
    <div>
      <Header title={'Update Tournament'} />
      <QueryRenderer query={query} variables={{id: props.match.params.tournamentId}} component={data => <ModifyTournamentRender {...data} />}/>
    </div>
  )
}

export default withRouter(ModifyTournament);