import { commitMutation } from 'react-relay'
import Environment from 'Services/Environment'
import ErrorUtils from 'Services/ErrorUtils'

function sendMuatation(variables, mutation, callback, props ={}){
  commitMutation(
    Environment,
    {
      mutation,
      variables,
      onCompleted: (response, errors) => {
        if(errors){
          //being handled in environment
          //ErrorUtils(errors)
        }
        callback(response, errors)
      },
      onError: err => {
        //ErrorUtils(err)
        callback(null, err)
      },
      ...props
    }
  )
}

export default sendMuatation