import React from 'react'
import CustomList from 'Components/CustomList'
import { useHistory } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'
import Switch from '@material-ui/core/Switch'
import { connect } from 'react-redux'
import {switchTheme} from 'Store/actions/independent'
import Header from 'Components/Header'
import AuthService from 'Store/actions/AuthService'

const styles = makeStyles(theme => ({
  container: {
    padding: theme.spacing(1),
    '& > *' :{
      marginBottom: theme.spacing(1)
    }
  }
}))

function AccountDetails(props){

  const classes = styles()
  const history = useHistory()

  const items = [
    {
      id: 1,
      primaryText: "Personal Details",
      to: "/account/details"
    },
    {
      id: 2,
      primaryText: "Transaction history",
      to: "/account/transactions"
    },
    {
      id: 3,
      primaryText: "Tournaments",
      to: "/account/tournaments"
    },
    {
      id: 4,
      primaryText: "Wallet",
      to: "/account/wallet"
    },
    {
      id: 5,
      primaryText: "Logout",
      to: "/account/auth/logout"
    },
    {
      id: 6,
      primaryText: "Set username",
      to: "/account/username"
    },
    {
      id: 7,
      primaryText: "Create Pages",
      to: "/account/create-page"
    }
  ]

  const logoutItems = [
    {
      id: 1,
      primaryText: "Login",
      to: "/account/auth/login"
    },
    {
      id: 2,
      primaryText: "Register",
      to: "/account/auth/register"
    }
  ]

  const siteDetails = [
    {
      id: 201,
      primaryText: "About Us",
      to: "/account/about-us"
    },
    {
      id: 202,
      primaryText: "Customer service",
      to: "/account/customer-service"
    },
    {
      id: 203,
      primaryText: "Terms of Service",
      to: "/account/terms-of-service"
    },
    {
      id: 204,
      primaryText: "Switch theme",
      secondaryIcon: <Switch onClick={props.switchTheme} checked={props.darkTheme}/>
    }
  ]

  function itemClick(item){
    if(item.id === 100){
      props.switchTheme()
      return
    }
    if(item.to){
      history.push(item.to)
    }
  }

  return (
    <div>
      <div className={classes.container}>
        <CustomList items={AuthService.isUserLoggedIn() ? items : logoutItems} button divider heading={"Account actions"} itemClick={itemClick}/>
        <CustomList items={siteDetails} button divider heading={"Site Options"} itemClick={itemClick}/>
      </div>
    </div>
  )

}

const mapStateToProps = state => ({
  darkTheme: state.Independent.darkTheme
})

const mapDispatchToProps = dispatch => ({
  switchTheme: () => dispatch(switchTheme())
})

export default connect(mapStateToProps, mapDispatchToProps)(AccountDetails)