import React from 'react'
import Form from 'Components/Form'
import { TextField } from '@material-ui/core'
import LoadingButton from 'Components/LoadingButton'
import graphql from 'babel-plugin-relay/macro'
import CommitRelay from 'Components/CommitRelay'
import ErrorUtil from 'Services/ErrorUtils'
import AuthService from 'Store/actions/AuthService'
import { useHistory } from 'react-router-dom'
import {Redirect} from 'react-router-dom'

const mutation = graphql`

mutation LoginMutation($data: LoginInput!){
  Login(user: $data){
    accessToken
    refreshToken
  }
}

`

function Login(){

  let [loading, setLoading] = React.useState(false)
  let history = useHistory()

  function onSubmit(data){
    setLoading(true)
    CommitRelay({data}, mutation, (res, err) => {
      setLoading(false)
      if(err !== null){
        return ErrorUtil(err)
      }else{
        AuthService.login({
          accessToken: res.Login.accessToken,
          refreshToken: res.Login.refreshToken,
          username: data.username
        })
        history.push("/")
      }
    })
  }
  if(AuthService.isUserLoggedIn()){
    console.log("It shoudl br redirected")
    //return <Redirect to={'/'} />
  }

  return (
    <Form onSubmit={onSubmit}>
      <TextField label={'Username'} name={'username'} fullWidth required/>
      <TextField label={'Password'} name={'password'} fullWidth type="password" required/>
      <LoadingButton type="submit" isLoading={loading}>Submit</LoadingButton>
    </Form>
  )
}

export default Login;