import { createStore, applyMiddleware, compose } from 'redux';
import app from './reducers'

/**
 * @name configureStore.js
 * @type { Stateless component }
 * @requires redux-thunk,redux-logger
 * @description: this is the function that returns a store object from redux.
 */

const localStorageMiddleware = ({getState}) => { // <--- FOCUS HERE
    return (next) => (action) => {
        const result = next(action);
        localStorage.setItem('gameBackend', JSON.stringify(
            {
                Independent: getState().Independent,
                Auth: getState().Auth
            }
        ));
        return result;
    };
};

let persistedStore = {}

try{
    persistedStore = localStorage.getItem('gameBackend') ? JSON.parse(localStorage.getItem('gameBackend')) : {}
}catch(err){
    console.log("Unable to read store from localstorage", err)
}

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const configureStore = () => {
    return createStore(app, persistedStore, composeEnhancers(applyMiddleware(localStorageMiddleware)));
};

export default configureStore();