import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import GameOptions from './Add/GameOptions'
import GameSpecific from './Add/GameSpecific'
import TournamentBasic from './Add/TournamentBasic'
import Prizes from './Add/PrizesTable'

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  backButton: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

function getSteps() {
  return ['Select Game', 'Custom fields',  'Prizes', 'Tournament details'];
}

export default function AddTournament() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const handleNext = () => {
    //console.log(activeStep)
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    //console.log("Active step", activeStep)
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  function getStepContent(stepIndex) {
    //console.log(stepIndex)
    switch (stepIndex) {
      case 0:
        return <GameOptions handleNext={handleNext}/>;
      case 1:
        return <GameSpecific handleNext={handleNext} handleBack={handleBack}/>;
      case 2:
        return <Prizes handleNext={handleNext} handleBack={handleBack}/>
      case 3:
        return <TournamentBasic handleNext={handleNext} handleBack={handleBack}/>;
      default:
        return 'Unknown stepIndex';
    }
  }

  //console.log("Normal", activeStep)

  return (
    <div className={classes.root}>
      <Stepper activeStep={activeStep} alternativeLabel>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <div>
        {getStepContent(activeStep)}
      </div>
    </div>
  );
}
