import React from 'react'
import { Grid } from '@material-ui/core'
import Notification from './Notification'
import Tournaments from './TournamentsSection'
import {makeStyles} from '@material-ui/core/styles'

const styles = makeStyles(theme => ({
  container: {
    width: '100%',
    margin: 0
  }
}))


function Holder() {
  let [tournament, setTournament] = React.useState({})
  const classes = styles()
  function selectTournament(item) {
    setTournament(item)
    //console.log(item)
  }


  return <Grid container spacing={1} className={classes.container}>
    <Grid item xs={12} sm={6}>  
      <Notification tournament={tournament}/>
    </Grid>
    <Grid item xs={12} sm={6}>
      <Tournaments selectTournament={selectTournament}/>
    </Grid>
  </Grid>
}

export default Holder