import React from 'react'
import PropTypes from 'prop-types'
import {FormControl, InputLabel, Select, Checkbox, MenuItem, ListItemText, Input, Chip} from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'

const styles = makeStyles({
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  chip: {
    margin: 2,
    marginLeft: 4,
  },
})

function MultiSelect(props){
  let a = []
  if(props.defaultValue !== undefined){
    a = props.defaultValue.split(",")
  }else if(props.value !== undefined){
    a = props.value.split(",")
  }
  //console.log(a, props.defaultValue, props.value)
  let [value, setValue] = React.useState(a)
  const classes = styles()
  const onChange = (event) => {
    setValue(event.target.value);
    props.onChange(event.target.value)
  };
  return (
    <FormControl fullWidth>
      <InputLabel id={props.labelId}>{props.label}</InputLabel>
      <Select
        labelId={props.labelId || "Multiple select"}
          id={props.id}
          multiple
          value={value}
          onChange={onChange}
          input={<Input name={props.name} />}
          renderValue={(selected) => (
            <div className={classes.chips}>
              {selected.map((value) => (
                <Chip key={value} label={value} className={classes.chip}/>
              ))}
            </div>
          )}
        >
          {props.options.map((option) => (
            <MenuItem key={option} value={option}>
              <Checkbox checked={value.indexOf(option) > -1} />
              <ListItemText primary={option} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
  )
}

MultiSelect.propTypes = {
  id: PropTypes.string,
  onChange: PropTypes.func,
  options: PropTypes.array.isRequired,
  labelId: PropTypes.string,
  name: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired
}

export default MultiSelect