import React from 'react'
import {withRouter, useHistory} from 'react-router-dom'
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import Button from 'Components/LoadingButton'
import * as yup from 'yup'
import { Box, Paper, TextField, IconButton } from '@material-ui/core';
import {makeStyles} from '@material-ui/core/styles'
import {DateTimePicker} from '@material-ui/pickers'
import MaterialTable from 'Components/MaterialTable'
import {toastInfo, toastSuccess} from 'Components/Toast'
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import ArrowBack from '@material-ui/icons/ArrowBack'

const mutation = graphql`
  mutation AddRoundMutation($data: MegladronGroupInput!){
    createMegladronGroup(data: $data) {
      _id
      rounds {
        title
        startTime
        endTime
        totalMatches
      }
    }
  }
`

const styles = makeStyles(theme => ({
  form: {
    padding: theme.spacing(1),
    '& > *': {
      marginBottom: theme.spacing(1)
    }
  }
}))

const schema = yup.object().shape({
  title: yup.string().required().max(30),
  matches: yup.number().integer().min(1)
})

function AddRound(props) {

  let [date, setDate] = React.useState({start: Date.now(), end: Date.now()})
  let [tableData, setTableData] = React.useState([])
  let history = useHistory()
  const {register, errors, handleSubmit} = useForm({
    resolver: yupResolver(schema)
  })

  const classes = styles()
  function submitForm(data) {
    console.log(date, date.start <= date.end, date.start > date.end)
    if(date.start >= date.end) {
      return toastInfo("Start time and end time cannot be same")
    }
    let toSend = {...data, totalMatches: +data.totalMatches, _id: props.match.params.id, endTime: date.end.toString(), startTime: date.start.toString(), custom: tableData.map(item => ({label: item.label, value: item.value}))}
    console.log(toSend)
    CommitRelay({data: toSend}, mutation, (response, error) => {
      if(!error){
        toastSuccess("Group addedd successflly")
        history.goBack()
      }
    })
  }
  return (
    <Box p={1}>
      <Paper component="form" onSubmit={handleSubmit(submitForm)} className={classes.form}>
        <IconButton onClick={() => history.goBack()}><ArrowBack /></IconButton> Go Back
        <TextField inputRef={register} name="title" label="Round title" error={errors.title !== undefined} helperText={errors.title? errors.title.message: "Could be something like prelims, Round 1"} required fullWidth/>
        <DateTimePicker onChange={start => setDate({...date, start: new Date(start).getTime()})} value={date.start} disablePast fullWidth label="Start date of round" name="startTime"/>
        <DateTimePicker onChange={end => setDate({...date, end: new Date(end).getTime()})} value={date.end} disablePast fullWidth label="End date of round" name="endTime"/>
        <TextField inputRef={register} name="totalMatches" fullWidth label="Number of matches" required error={errors.totalMatches !== undefined} helperText={errors.totalMatches?.message} />
        <MaterialTable 
          title={"Any round specific data"} 
          tableData={tableData} 
          getTableData={data => setTableData(data)}
          columns={[
            {
              title: "Label", field: "label"
            },
            {
              title: "Value", field: "value"
            }
          ]}
        />
        <Button isLoading={false} type="submit">Add Round</Button>
      </Paper>
    </Box>
  )
}

export default withRouter(AddRound)