import React from 'react'
import { useHistory } from 'react-router-dom'
import { Grid } from '@material-ui/core'
import GameCard from 'Components/GameCard'
import { makeStyles } from '@material-ui/core/styles'
import Header from 'Components/Header'
import Add from '@material-ui/icons/Add'
import graphql from 'babel-plugin-relay/macro'
import RenderRelay from 'Components/QueryRenderer'
import CommitMutation from 'Components/CommitRelay'
import DeleteQuery from './DeleteGame'
import GamesList from './GamesList'

export const getGamesQuery = graphql`
query ViewGamesQuery{
  games{
    ...GamesList_games
  }
}
`

const styles = makeStyles(theme => ({
  root: {
    position: "relative"
  }
}))

function ViewGames(){
  const history = useHistory()
  return (
    <RenderRelay
      query={getGamesQuery}
      component={(data) => <GamesList {...data} history={history}/>}
    />
  )
}

function ViewGamesRender(props) {
  const history = useHistory()
  const classes = styles()
  //writeGamesInStorage()
  function deleteItem(id){
    if(!window.confirm("Do you wish to delete")){
      return;
    }
    CommitMutation({id}, DeleteQuery, (response, errors) => {
      console.log(response, errors)
    }
    //optimisticupdater
    )
  }

  return (
    <div className={classes.root}>
      <Grid container>
        {
          props.games.map(item => <GameCard key={item._id} {...item} deleteItem={deleteItem}/>)
        }
      </Grid>
    </div>
  )
}


export default ViewGames