import React from 'react'
import Modal from 'Components/Modal'
import {Button} from '@material-ui/core'
import { createContainer } from "unstated-next"
import MaterialTable from 'Components/MaterialTable'
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import {toastSuccess} from 'Components/Toast'

const mutation = graphql`
  mutation CredentialsModalMutation($data: UpdateCredentialsInput!){
    updateCredentials(data: $data) {
      tournament {
        _id
        credentials{
          label
          value
        }
      }
    }
  }
`

function useModal(initialState = {open: false}) {
  let [modal, setModal] = React.useState(initialState)
  let openModal = () => setModal({open: true})
  let closeModal = () => setModal({open: false})
  return {modal, openModal, closeModal}
}

export const ModalState  = createContainer(useModal)

function CredentailsModal(props) {
  
  let [tableData, setTableData] = React.useState([])
  let {modal, openModal, closeModal} = ModalState.useContainer()
  function handleTableData(data){
    setTableData(data)
  }

  function handleSubmit(){
    console.log(tableData)
    let data = {id: props.id, credentials: tableData.map(item => ({label: item.label, value: item.value}))}
    CommitRelay({data}, mutation, (response, errors) => {
      if(!errors){
        toastSuccess("Updated credentials")
      }
    })
  }

  return (
    <React.Fragment>
        <Button variant="contained" color="primary" onClick={() => openModal()}>Add Credentials</Button>
        <Modal open={modal.open} title={'Add Game credentials'} handleClose={() => closeModal()} handleSubmit={handleSubmit}>
          <MaterialTable title="Enter details" columns={[
            {title: "Label", field: "label"},
            {title: "Value", field: "value"}
          ]} tableData={tableData} getTableData={handleTableData}/>
        </Modal>
    </React.Fragment>
  )
}

export default CredentailsModal