import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import {Grid, Paper, Typography, Button}  from '@material-ui/core'
import CustomList from 'Components/CustomList'
import Table from 'Components/Table'
import {LinearProgressWithLabel} from './Comps/NewTournamentCard'
import {Link} from 'react-router-dom'
import Modal, {ModalState} from './CredentialsModal'

const styles = makeStyles(theme => ({
  container: {
    padding: theme.spacing(1)
  },
  imageBackground: {
    height: 180,
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: "center center"
  },
  quickAction: {
    display: 'flex',
    padding: theme.spacing(1),
    justifyContent: "space-between",
    alignItems: "center"
  },
  spanClass: {
    display: "flex",
    alignItems: "center"
  },
  buttonContainer: {
    width: "100%",
    display: "flex",
    justifyContent: "space-between"
  },
  overview: {
    '&> *': {
      marginBottom: theme.spacing(1)
    }
  },
  title: {
    paddingTop: theme.spacing(2),
    paddingLeft: theme.spacing(2)
  }
}))

function getPreparedData(tournament){
  let prizes = [[]]
  let players = [[]]
  let items = []
  let credentials = []
  if(tournament.prizes){
    prizes = tournament.prizes.map(item => [item.position, " "+item.prize])
  }else {
    prizes = [{primaryText: "This tournament has no prizes"}]
  }
  if(tournament.registered)
    players = tournament.registered.map((item, index) => {return[index+1, item.gamingUsername]})
  if(tournament.customFields){
    items = tournament.customFields.map(item => ({primaryText: item.label, secondaryIcon: item.value, id: item.value}))
  }else {
    items = [{primaryText: "No game details were provided"}]
  }
  if(tournament.credentials){
    credentials = tournament.credentials.map(item => ({primaryText: item.label, secondaryIcon: item.value, id: item.label}))
  }else{
    credentials = [{primaryText: "15 min before tournament credentials will be update"}]
  }
  return {prizes, players, items, credentials}
}

function ModifyTournamentRender({specificTournament, ...props}){
  const classes = styles()
  //console.log(props)

  if(specificTournament === null || specificTournament === undefined){
    return <div>Tournament not available</div>
  }

  let {tournament} = specificTournament
  let {prizes, players, items, credentials} = getPreparedData(specificTournament.tournament)
  return <div>
      <div className={classes.imageBackground} style={{backgroundImage: `url("${tournament.image}")`}}></div>
      <div className={classes.container}>
        <Typography variant="h5">
          {tournament.title}
        </Typography>
          <Typography variant="body1" color="textSecondary">
            {tournament.description}
          </Typography>
        <Grid container spacing={1}>
            <Grid item xs={12} md={4} className={classes.overview}>
              <Paper>
                <div className={classes.quickAction}>
                  <div className={classes.buttonContainer}>
                    <Link to={`/tournaments/${tournament._id}/update`}><Button variant="contained" color="primary"> Update results</Button> </Link>
                    <ModalState.Provider>
                      <Modal id={tournament._id}/>
                    </ModalState.Provider>
                  </div>
                </div>
                <div style={{padding: 8}}>
                  <LinearProgressWithLabel label={`${tournament.totalRegistered}/${tournament.maxPlayers}`} value={tournament.totalRegistered / tournament.maxPlayers * 100}/>
                </div>
              </Paper>
              <CustomList items={credentials}  divider heading="Game credentials" />
              <CustomList items={items} divider heading="Tournament details"/>
              <Paper>
                <Typography color="textSecondary" className={classes.title}>Prizes</Typography>
                <Table heading={['Position', "Prize"]} body={prizes}/>
              </Paper>
            </Grid>
          <Grid item xs={12} md={8}>
            <Paper>
              <Typography color="textSecondary" className={classes.title}>Players Registered</Typography>
              <Table heading={['No.', "Player Id"]} body={players}/>
            </Paper>
          </Grid>
        </Grid> 
      </div>
    </div>
}

export default ModifyTournamentRender;