import React from 'react'
import { Switch, Route } from 'react-router-dom'
import AddTournaments from './AddTournaments'
import ViewTournaments from './ViewTournaments'
import ModifyTournament from './ModifyTournament/Modify'
import UpdateResults from './ModifyTournament/UpdateResults'

function Tournaments(props){

  return (
    <Switch>
      <Route path="/tournaments/:tournamentId/update" component={UpdateResults} />
      <Route path="/tournaments/add" component={AddTournaments} />
      <Route path="/tournaments/:tournamentId" component={ModifyTournament} />
      <Route path={'/tournaments'} component={ViewTournaments} />
    </Switch>
  )
}

export default Tournaments