import React from 'react'
import { createRefetchContainer } from 'react-relay'
import graphql from 'babel-plugin-relay/macro'
import CustomList from 'Components/CustomList'
import {Box} from '@material-ui/core'
import {Pagination} from '@material-ui/lab'
import makeDate from 'Utils/MakeDate'
import {makeStyles} from '@material-ui/core/styles'
import {useHistory} from 'react-router-dom'
import Error from 'Components/Error'
//import Request from './Request'

const styles = makeStyles(theme => ({
  pagination: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1)
  },
  paginationContainer: {
    display: "flex",
    justifyContent: "center"
  }
}))

const transformData = data => {
  return data.map(item => ({
    id: item._id,
    primaryText: item.userId.username,
    secondaryText: "Requested on "+makeDate(item.createdAt)+" to withdraw Rs."+item.amount+" via "+item.method
  }))
}

const pageLimit = 2

function RequestComponent(props) {
  const classes = styles()
  let history = useHistory()
  console.log(props)
  if(!props.getWithdrawRequests){
    return <Error />
  }

  let items = transformData(props.getWithdrawRequests.docs)

  function itemClick(item) {
    history.push('/wallet/'+item.id)
  }

  function handleChange(e, page) {
    //console.log({page, limit: 2})
    props.relay.refetch(
      {pagination: {page, limit: 30}}
    )
  }

  return <div>
    <Box p={1}>
      <CustomList items={items} button heading={'Withdraw Requests'} itemClick={itemClick}/>
      <div className={classes.paginationContainer}>
        <Pagination count={props.getWithdrawRequests.totalPages} color={"primary"} variant={'outlined'} onChange={handleChange} className={classes.pagination} />
      </div>
    </Box>
  </div>
}

export default createRefetchContainer(RequestComponent, {
  getWithdrawRequests: graphql`
    fragment RequestComponent_getWithdrawRequests on PaginatedWithdrawResponse{
      docs {
        _id
        userId {
          username
        }
        amount
        method
        createdAt
        approved
      }
      totalPages
      page
    }
  `
}, graphql`
query RequestComponentQuery($pagination: MongoosePagination){
  getWithdrawRequests(pagination: $pagination){
    ...RequestComponent_getWithdrawRequests
  }
}
`)