import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Account from './Account'
import TournamentRoutes from './Tournament'
import Games from './Games'
import { makeStyles } from '@material-ui/core/styles'
import Home from './Home'
import Wallet from './Home/Wallet/DetailedReport'
import Drawer from './Drawer'
import Notification from './Notifications/Holder'
import Mega from './Mega'

const styles = makeStyles(theme => ({
  toolbar: theme.mixins.toolbar,
  container: {
    width: '100%'
  },
  content: {
    height: `calc(100vh - ${theme.mixins.toolbar.height}px)`,
    width: '100%',
    [theme.breakpoints.up('md')]: {
      paddingLeft: 240,
    },
  }
}))

function Layout(){
  const classes = styles()
  return(
    <div>
      <Drawer />
      <div className={classes.toolbar} />
      <div className={classes.content}>
        <Switch>
          <Route path={'/account'} component={Account} />
          <Route path={'/tournaments'} component={TournamentRoutes} />
          <Route path={'/games'} component={Games}/>
          <Route path={'/wallet/:id'} component={Wallet} />
          <Route path={'/notification'} component={Notification} />
          <Route path={'/mega'} component={Mega} />
          <Route path={'/'} component={Home} />
        </Switch>
      </div>
    </div>
  )
}

export default Layout