import React from 'react'
import { connect } from 'react-redux'
import CustomFields from 'Components/MaterialTable'
import { setPrizes } from 'Store/actions/AddTournament'
import Button from '@material-ui/core/Button'
import Box from '@material-ui/core/Box'

function Prizes(props){

  let [tableData, setTableData] = React.useState(props.prizes || [])

  function handleData(data){
    setTableData(data)
  }

  // function handleData(data){
  //   props.setPrizes(data)
  // }

  function moveNext(){
    props.setPrizes(tableData)
    props.handleNext()
  }

  return (
    <React.Fragment>
      <Box p={1}>
        <CustomFields columns={[
          {title: "Position", field: "position"},
          {title: "Prize", field: "prize", type:"currency"}
        ]} title={"Game position and prizes"} tableData={tableData} getTableData={handleData}/>
      </Box>
      <Box p={1}>
        <Button
          onClick={props.handleBack}>
            Back
        </Button>
        <Button variant="contained" color="primary" onClick={moveNext} type={'button'}>
          next
        </Button>
      </Box>
    </React.Fragment>
  )
}

const mapStateToProps = state => ({
  prizes: state.AddTournament.prizes
})

const mapDispatchToProps = dispatch => ({
  setPrizes : data => dispatch(setPrizes(data))
})

export default connect(mapStateToProps, mapDispatchToProps)(Prizes);