import React from 'react'
import { createContainer } from "unstated-next"
import {withRouter, useHistory} from 'react-router-dom'
import Menu from '@material-ui/icons/Menu'
import ArrowBack from '@material-ui/icons/ArrowBack'
import { makeStyles } from '@material-ui/core/styles'
import IconButton from '@material-ui/core/IconButton'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Select from 'Layout/FormBuilder/Select'
import {environment} from 'Store/actions/independent'
import {connect} from 'react-redux'

const drawerWidth = 240;

const styles  = makeStyles(theme => ({
  appBar: {
    backgroundColor: theme.palette.background.paper,
    [theme.breakpoints.up('md')]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  menuButton: {
    marginRight: theme.spacing(1),
    [theme.breakpoints.up('md')]: {
      display: 'none',
    }
  },
  headerText: {
    flex: 1,
    color: "#212121"
  },
  icon: {
    color: "#fff"
  }
}))

function Header(props){
  const classes = styles()

  return (
    <AppBar position="fixed" className={classes.appBar}>
      <Toolbar>
        <IconButton onClick={props.iconClick} className={classes.menuButton}>
          {props.icon}
        </IconButton>
        <Typography variant="h6" noWrap className={classes.headerText}>
          {props.environment}
        </Typography>
        <div style={{width: 150}}>
          <Select label="Select Environemnt" name="environment" defaultValue={props.environment} options={["Testing", "Production", "Local"]} onChange={e => environment(e.target.value)}/>
        </div>
      </Toolbar>
    </AppBar>
  )
}

const mapStateToProps = state => ({
  environment: state.Independent.environment
})

const _Header = connect(mapStateToProps)(Header)

function useDrawer() {
  let [drawerOpen, setDrawerOpen] = React.useState(false)
  let setDrawer = (open) => setDrawerOpen(open)
  return {drawerOpen, setDrawer}
}

export const DrawerState = createContainer(useDrawer)

function _AppBar(props) {
  let drawer = DrawerState.useContainer();
  let history = useHistory();

  function iconClick() {
    if(props.location.pathname.replace(/[^\/]/g, "").length === 1){
      drawer.setDrawer(true)
    }else {
      history.goBack()
    }
  }

  return  <_Header 
    icon={props.location.pathname.replace(/[^\/]/g, "").length === 1 ? <Menu/> : <ArrowBack/>} 
    iconClick={iconClick}
    />
}

export default withRouter(_AppBar)