import store from 'Store'

const page = "AUTH::"

const Auth = {
  login: data => {
    store.dispatch({
      type: page+"LOGIN",
      data
    })
  },
  getData: () => {
    return store.getState().Auth
  },

  addTournament: (id) => {
    return store.dispatch({
      type: page+"ADD_TOURNAMENT",
      id
    })
  },

  logout: () => {
    store.dispatch({
      type: page+"LOGOUT"
    })
  },
  isUserLoggedIn: () => {
    return store.getState().Auth.accessToken !== ""
  },
  updateBalance: (wallet, bonus) => {
    store.dispatch({
      type: page+"UPDATE_BALANCE",
      wallet,
      bonus
    })
  }
}

export default Auth
