import React from 'react'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Form from 'Components/Form'
import { connect } from 'react-redux'
import { resetData } from 'Store/actions/AddTournament'
import { snackbarSuccess } from 'Store/actions/snackbar'
import { useHistory } from 'react-router-dom'
import graphql from 'babel-plugin-relay/macro'
import CommitRelay from 'Components/CommitRelay'
import { toastSuccess } from 'Components/Toast'

const query = graphql`
  mutation TournamentBasicMutation($data: TournamentInput) {
    addTournament(tournamentFields: $data){
      tournament {
        _id
        title
      }
    }
  }
`


function TournamentBasic(props) {

  let history = useHistory()

  function onSubmit(data){
    let prizes = props.tournament.prizes;
    let customFields = props.tournament.customFields
    let game = props.tournament.game
    if(!prizes || prizes.length === 0){
      if(!window.confirm("Are you sure about a tournament without prizes? If you forgot to add. It's on second screen")){
        return
      }
    }
    let inputPrizes = prizes === undefined ? [] : prizes.map(p => ({prize: +p.prize, position: p.position}))
    // for(let key in customFields){
    //   customFieldsInput.push({
    //     label: key,
    //     value: customFields[key]
    //   })
    // }
    //console.log(prizes, customFields, game, data)
    CommitRelay({
      data: {
        title: data.title,
        description: data.description,
        time: data.time,
        entryFee: +data.entryFee,
        maxPlayers: +data.maxPlayers,
        gameId: game._id,
        image: data.image,
        customFields: customFields,
        prizes: inputPrizes,
        metricsPoints: props.tournament.metrics,
        maxGroups: +data.maxGroups
      }
    }, query, (response, errors) => {
      if(!errors){
        toastSuccess("Tournament created succefully")
        history.push("/tournaments")
      }
    })
  }
  let time = new Date().toISOString().substr(0, 16)
  console.log(time)
  //date.setDate(+date.getDate() + 1)
  //console.log(date.getMonth(), date.getDate())
  //let time = date.getFullYear()+"-"+(date.getMonth() < 10 ? "0"+date.getMonth()  : date.getMonth())+"-"+(date.getDate() < 10 ? "0"+date.getDate()  : date.getDate())
  //console.log(time, props.tournament.game.image)
  return (
    <div style={{padding: 8}}>
      <Form onSubmit={onSubmit}>
        <TextField label={'Tournament title'} name="title" fullWidth required defaultValue={props.tournament.game.title+" Tournament #"+props.tournament.game.totalTournaments} helperText={'Default values are cool. But, rename it if you can'}/>
        <TextField type="datetime-local" name="time" fullWidth label="Time of Tournament Format(mm/dd/yyyy)" defaultValue={time} required/>
        <TextField label={'Entry Fee'}  fullWidth name="entryFee" type="number" required/>
        <TextField type="number" name="maxPlayers" label={'Max Participants'} fullWidth type="number" required/>
        <TextField name="image" fullWidth label={"Image URL"} defaultValue={props.tournament.game.image+" "}/>
        <TextField type="number" name="maxGroups" fullWidth label={"Max groups that could be formed"} helperText={"If no grouping enter same number as max players"} required/>
        <TextField label={'Tournament Description'} fullWidth multiline rows={5} name="description" helperText={"Add things like rules or anything specific"} required/>
        <div style={{padding: 8}}>
          <Button
            onClick={props.handleBack}
            >
            Back
          </Button>
          <Button variant="contained" color="primary" type={'submit'}>
            Submit
          </Button>
        </div>
      </Form>
    </div>
  )
}

const mapStateToProps = state => ({
  tournament: state.AddTournament
})

const mapDispatchToProps = dispatch => ({
  resetData: () => dispatch(resetData()),
  snackbarSuccess: msg => dispatch(snackbarSuccess(msg))
})

export default connect(mapStateToProps, mapDispatchToProps)(TournamentBasic)