import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import {Paper, Grid, Box, LinearProgress, Typography, Button, Divider} from '@material-ui/core'

const styles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(1)
  },
  textHolder: {
    padding: theme.spacing(1)
  },
  time: {
    display: "block",
    padding: "4px 16px",
    backgroundColor: theme.palette.primary.main,
    color: "#fff",
    textAlign: "center"
  },
  image: {
    height: theme.spacing(7),
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    backgroundSize: "cover",
    backgroundPosition: "center"
  }
}))

export function LinearProgressWithLabel(props) {
  return (
    <Box display="flex" alignItems="center">
      <Box width="100%" mr={1}>
        <LinearProgress variant="determinate" {...props} />
      </Box>
      <Box minWidth={35}>
        <Typography variant="body2" color="textSecondary">{props.label}</Typography>
      </Box>
    </Box>
  );
}

function TournamentCard(props){

  const classes = styles()

  return (
    <div className={classes.root}>
      <Paper>
        <Grid container alignItems={'center'} justify={'center'}>
          <span className={classes.time}>Jun 27, 9:00 PM</span>
        </Grid>
        <Grid container className={classes.textHolder}>
          <Grid item xs={2}>
            <div className={classes.image} style={{backgroundImage: 'url("https://pngimg.com/uploads/pubg/pubg_PNG27.png")'}}>
            </div>
          </Grid>
          <Grid item xs={10}>
            <Typography variant="body1" color="textPrimary">Pubg Tournament</Typography>
          </Grid>
          <Grid item xs={12}>
            <LinearProgressWithLabel value={12} color="secondary"/>
          </Grid>
        </Grid>
        <Divider />
        <Grid container className={classes.textHolder} justify={'space-between'}>
          <Grid item> </Grid>
          <Grid item>
            <Button variant="contained" color="secondary" size="small">Join 10</Button>
          </Grid>
        </Grid>
      </Paper>
    </div>
  )
}

export default TournamentCard