import React from 'react'
import { connect } from 'react-redux'
import FormBuilder from 'Layout/FormBuilder'
import {Typography, Paper, Button, TextField, Checkbox} from '@material-ui/core'
import {addGameSpecificData, setMetrics} from 'Store/actions/AddTournament'
import { makeStyles } from '@material-ui/core/styles'
import { snackbarError } from 'Store/actions/snackbar'
import graphql from 'babel-plugin-relay/macro'
import QueryRenderer from 'Components/QueryRenderer'
import { toastError } from 'Components/Toast'

const styles = makeStyles(theme => ({
  container: {
    padding: theme.spacing(1)
  },
  paper: {
    padding: theme.spacing(1)
  },
  row: {
    display: "flex",
    flexDirection: "row"
  }
}))


const gameSpecificQuery = graphql`
query GameSpecificQuery($title: String!){
  specificGame(title: $title) {
    customFields {
      label
      type
      options
    }
    metrics
  }
}
`

function GameSpecific(props) {
  const classes = styles()
  if(props.game._id === undefined){
    toastError("Set a game first")
    return (<div></div>)
  }

  function handleChange(e){
    //props.setCustomFields(e.target.name, e.target.value)
  }

  function moveForward() {
    let el = document.querySelector("#gameDetails")
    if(!el){
      toastError("Root element not found. Unable to read details. Cannot proceed")
      return
    }
    let inputs = el.querySelectorAll("[name]")
    let data = []
    //let formData = new FormData()
    for(let i = 0; i < inputs.length; i++){
      if(inputs[i].getAttribute("type") === "checkbox") {
        data.push({
          label: inputs[i].getAttribute("name"),
          value: inputs[i].checked ? "Yes" : "No"
        })
      }else {
        data.push({
          label: inputs[i].getAttribute("name"),
          value: inputs[i].value 
        })
      }
      //formData.append(inputs[i].getAttribute("name"), inputs[i].value)
    }
    props.addGameSpecificData(data)

    //metrics calculation
    el = document.querySelector("#metrics")
    if(!el){
      toastError("Metrics not found. Unable to read details. Cannot proceed")
      return
    }
    inputs = el.querySelectorAll("[name]")
    let metrics = []
    for(let i = 0; i < inputs.length; i++){
      let name = inputs[i].getAttribute("name")
      if(name.indexOf("dynamic") !== -1){
        continue
      }
      metrics.push({
        label: name,
        points: inputs[i].value === "" ? 0 : +inputs[i].value,
        dynamic: el.querySelector(`[name=${name}_dynamic]`).checked === true ? 1 : 0
      })
    }
    props.setMetrics(metrics)
    //console.log(metrics)
    props.handleNext()
  }

  return (
    <div className={classes.container}>
      <Paper className={classes.paper}>
        <Typography>All the custom fields available for {props.game.title}</Typography>
        <div>
          <QueryRenderer 
            query={gameSpecificQuery}
            variables={{title: props.game.title}}
            component={(data) => <CustomFields {...data} handleChange={handleChange} customFields={props.customFields}/>}
          />
        </div>
      </Paper>
      <div style={{padding: 8}}>
        <Button
          onClick={props.handleBack}
          >
          Back
        </Button>
        <Button variant="contained" color="primary" onClick={moveForward} type={'button'}>
          next
        </Button>
      </div>
    </div>
  )
}

let Row = props => <div className={props.classes.row}>
  <span style={{flex: 1}}> <TextField name={props.name} type="number" label={props.label} fullWidth helperText={props.helperText}/></span>
  <span><Checkbox name={props.name+"_dynamic"}/>Dynamic</span>
</div>

function getValue(name, data) {
  for(let i=0; i< data.length; i++){
    if(data[i].label === name){
      return data[i].value
    }
  }
}

function CustomFields(props){
  const classes = styles()
  let customFields = props.specificGame.customFields.map(item => (
    {
      ...item, 
      name: item.label, 
      defaultValue: getValue(item.label, props.customFields)
    }))
  let metrics = props.specificGame.metrics.map(item => ({
    label: item,
    name: item,
    type: "number",
    helperText: "Enter -1 if it won't affect total score."
  }))
  //console.log(customFields)
  return (
    <div>
      <FormBuilder fields={customFields} id={"gameDetails"}/>
      <Typography variant="h5" color="textSecondary">How players will be scored</Typography>
      <div id="metrics">
      {metrics.map(item => <Row {...item} key={item.name} classes={classes}/>)}
      <Typography color="textSecondary" variant="body2">
      Enable dynamic calculation to distribute points in group exponentialy. Like a player with higher position will earn higher points compared to lower ones.
      </Typography>
      </div>
    </div>
  )
}

const mapStateToProps = state => ({
  customFields: state.AddTournament.customFields,
  game: state.AddTournament.game
})

const mapDispatchToProps = dispatch => ({
  addGameSpecificData: (data) => dispatch(addGameSpecificData(data)),
  snackbarError: msg => dispatch(snackbarError(msg)),
  setMetrics: (data) => dispatch(setMetrics(data))
})

export default connect(mapStateToProps, mapDispatchToProps)(GameSpecific)