

export default {

  setToken: (accessToken, refreshToken) => {
    localStorage.setItem("auth", JSON.stringify({accessToken, refreshToken, tokenExpired: false}))
  },

  isUserLoggedIn: () => {
    let auth = localStorage.getItem('auth')
    if(auth === null && auth !== ""){
      return false
    }
    try{
      auth = JSON.parse(auth)
    }catch(err){
      return false
    }
    if(auth.tokenExpired !==undefined && !auth.tokenExpired){
      return true
    }
    return false
  },

  logout: () => {
    localStorage.setItem("auth", JSON.stringify({accessToken: "", refreshToken: "", tokenExpired: true}))
  },

  getAccessToken: () => {
    let auth = localStorage.getItem('auth')
    if(auth === null){
      return false
    }

    try{
      auth = JSON.parse(auth)
    }catch(err){
      return false
    }
    if( !auth.tokenExpired ){
      return auth.accessToken
    }
    return false
  },

  getRefreshToken: () => {
    let auth = localStorage.getItem('auth')
    if(auth === null){
      return false
    }

    try{
      auth = JSON.parse(auth)
    }catch(err){
      return false
    }
    if(!auth.tokenExpired ){
      return auth.refreshToken
    }
    return false
  }

}