import React from 'react'
import { connect } from 'react-redux'
import { setGame } from 'Store/actions/AddTournament'
import { makeStyles } from '@material-ui/core/styles'
import RenderRelay from 'Components/QueryRenderer'
import CustomList from 'Components/CustomList'
import graphql from 'babel-plugin-relay/macro'

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(1)
  },
}));


function GameOptions(props){

  //console.log(props)

  if(props.games === null || props.games === undefined) {
    return <div>Games not found</div>
  }

  function itemClick(item){
    props.setGame(item)
    props.handleNext()
  }
  //console.log(props.games)

  let items = props.games.map(item => ({id: item._id, primaryText: item.title, ...item}))
  return (
    <div>
      <div style={{padding: 8}}>
        <CustomList items={items} button heading={'Select a game'} itemClick={itemClick}/>
      </div>
    </div>
  )
}


const mapDispatchToProps = dispatch => ({
  setGame: (data) => dispatch(setGame(data))
})

const Gamee =  connect(null, mapDispatchToProps)(GameOptions)

export default function Gameee(props){
  return (
    <RenderRelay 
      query={graphql`
        query GameOptionsQuery {
          games {
            title
            _id
            id
            image
            totalTournaments
          }
        }
      `}
      component={(data) => <Gamee {...data} {...props}/>}
    />
  )
}