import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import QueryRenderer from 'Components/QueryRenderer'
import Fab from 'Components/Fab'
import {useHistory} from 'react-router-dom'
import CustomList from 'Components/CustomList'
import { Box } from '@material-ui/core'

const query = graphql`
  query ViewMegladronQuery{
    megladron {
      docs {
        _id
        title
      }
    }
  }
`

function ViewMegladron(props) {
  return (
    <QueryRenderer query={query} component={data => <ViewMegladronComponent {...data} />}/>
  )
}

function ViewMegladronComponent(props) {
  let history = useHistory()
  let items = props.megladron.docs.map(item => ({id: item._id, primaryText: item.title}))

  function itemClick(item) {
    history.push("/mega/view/"+item.id)
  }

  return (
    <Box p={1}>
      <CustomList items={items} button divider itemClick={itemClick} heading="Mega tournaments"/>
      <Fab onClick={() => history.push("/mega/add")} />
    </Box>
  )
}

export default ViewMegladron