import React from 'react'
import MateriaTable from 'Components/MaterialTable'
import { Box, Button } from '@material-ui/core'

function Prizes(props) {

  let [tableData, setTableData] = React.useState([])

  function getTableData(data){
    setTableData(data)
  }

  function moveForward() {
    props.getTableData(tableData.map(item => ({position: item.position, prize: +item.prize})))
  }

  return (
    <>
      <MateriaTable columns={[
        {title: "Position", field: "position"},
        {title: "Prize", field: "prize", type:"currency"}
      ]} title={"Prizes or points awarded"} tableData={tableData} getTableData={getTableData} />
      <Box p={1}>
        <Button
          onClick={props.handleBack}
          >
          Back
        </Button>
        <Button variant="contained" color="primary" type={'submit'} onClick={moveForward}>
          next
        </Button>
      </Box>
    </>
  )
}

export default Prizes