import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Login from './Auth/Login'
import Register from './Auth/Register'
import AccountDetails from './AccountDetails'
import Tournaments from './AccountDetails/AccountTournaments'
import PersonalDetails from './AccountDetails/PersonalDetails'
import Wallet from './AccountDetails/Wallet'
import Transactions from './AccountDetails/Transactions'
import Logout from './Auth/Logout'
import AboutUs from './AccountDetails/AboutUs'
import TermsOfService from './AccountDetails/TermsOfService'
import CustomerService from './AccountDetails/CustomerService'

function Account(){
  return(
    <Switch>
      <Route path={'/account/auth/login'} component={Login} />
      <Route path={'/account/auth/register'} component={Register} />
      <Route path={'/account/auth/logout'} component={Logout} />
      <Route path="/account/tournaments" component={Tournaments} />
      <Route path={"/account/details"} component={PersonalDetails} />
      <Route path={"/account/wallet"} component={Wallet} />
      <Route path={"/account/transactions"} component={Transactions} />
      <Route path={"/account/about-us"} component={AboutUs} />
      <Route path={'/account/terms-of-service'} component={TermsOfService} />
      <Route path={'/account/customer-service'} component={CustomerService} />
      <Route path={'/account'} component={AccountDetails} />
    </Switch>
  )
}

export default Account