import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import QueryRenderer from 'Components/QueryRenderer'
import Select from 'Layout/FormBuilder/Select'

const query = graphql`
  query GamesAvailableQuery{
    games{
      title
      _id
    }
  }
`

function GamesAvailable(props) {
  return <QueryRenderer query={query} component={data => <GamesAvailableComponent {...data} />} />
}

function GamesAvailableComponent(props) {
  let options = props.games.map(item => ({label: item.title, value: item._id}))
  return (
    <Select options={options} name={"game"} label={"Select a tournament game"}/>
  )
}

export default GamesAvailable