import { initialState } from './index'

const AuthReducer = (state = initialState.Auth, action) => {
  switch(action.type) {
    case "AUTH::LOGIN":
      return {
        ...action.data
      }
    case "AUTH::LOGOUT":
      return {
        ...initialState.Auth
      }
    case "AUTH::ADD_TOURNAMENT":
      return {
        ...state,
        registeredTournaments: [...state.registeredTournaments, action.id]
      }
    case "AUTH::UPDATE_BALANCE": 
      return {
        ...state,
        walletBalance: action.wallet,
        bonus: action.bonus
      }
    default:
      return state
  }
}

export default AuthReducer