import React from 'react'
import Form from 'Components/Form'
import { TextField } from '@material-ui/core'
import LoadingButton from 'Components/LoadingButton'
import graphql from 'babel-plugin-relay/macro'
import CommitRelay from 'Components/CommitRelay'
import {toastInfo} from 'Components/Toast'
import {Redirect} from 'react-router-dom'
import AuthService from 'Services/AuthService'

const mutation = graphql`
  mutation RegisterMutation($data: UserInput!){
    Register(user: $data) {
      user {
        _id
      }
      refreshToken
      accessToken
    }
  }
`

function Register(){

  let [registering, setRegistering] = React.useState(false)

  function onSubmit(data){
    if(data.phone.toString().length !== 10){
      return toastInfo("Phone number should be 10 characters")
    }
    setRegistering(true)
    CommitRelay({data}, mutation, responseHandler)
  }

  function responseHandler(response, errors){
    setRegistering(false)
    console.log(response)
  }

  if(AuthService.isUserLoggedIn()){
    return <Redirect to={'/'} />
  }

  return (
    <Form onSubmit={onSubmit}>
      <TextField label={'Email'} name="username" type="email" required fullWidth/>
      <TextField label={'Phone'} type="number" min={10} max={10} required fullWidth name="phone" />
      <TextField label={'Password'} type={"password"} required fullWidth name="password" />
      <LoadingButton isLoading={registering} type="submit">Register</LoadingButton>
    </Form>
  )
}

export default Register