import React from 'react'
import { Paper, Grid, Typography } from '@material-ui/core'
import { makeStyles }  from '@material-ui/core/styles'

const styles = makeStyles(theme => ({
  root: {
    
  },
  letterHolder: {
    background: "#efefef",
    fontSize: 32,
    color: "#212121",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    height: "100%"
  },
  textContainer: {
    padding: theme.spacing(1)
  },
  paragraph: {
    margin: 0
  }
}))

function TournamentComp(props){
  
  const classes = styles()

  return <Paper>
    <Grid container alignItems={'stretch'}>
      <Grid item xs={2}>
        <div className={classes.letterHolder}>{props.title.substr(0, 1)}</div>
      </Grid>
      <Grid item xs={10} className={classes.textContainer}>
        <Typography variant="h5" color="textSecondary">{props.gameId.title}</Typography>
        <Grid container>
          <Grid item xs={4}>
            <p className={classes.paragraph}>Entry Fee</p>
            {props.entryFee}
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  </Paper>
}

export default TournamentComp