import React from 'react'
import {withRouter, Route, useHistory} from 'react-router-dom'
import { Paper } from '@material-ui/core'
import Fab from 'Components/Fab'
import {makeStyles} from '@material-ui/core/styles'
import AddTournament from './AddTournament/AddTournament'
import {createFragmentContainer} from 'react-relay'
import graphql from 'babel-plugin-relay/macro'

const styles = makeStyles(theme => ({
  paper: {
    marginBottom: theme.spacing(1),
    position: "relative",
    padding: theme.spacing(1)
  },
  title: {
    textTransform: "uppercase",
    fontSize: 24,
    display: "inline-block",
    marginBottom: theme.spacing(1)
  }
}))

function TournamentsInRounds(props){
  const classes = styles()
  const history = useHistory()
  return (
    <Paper className={classes.paper}>
      <span className={classes.title}>{props.round.primaryText}</span> <br />
      {props.round.secondaryText} <br />
      Total of {props.round.totalMatches} tournaments will be generated
      <Route path={'/mega/view/:id/addTournament'} component={() => <AddTournament game={props.game}/>}/>
      <Fab position="absolute" title="Add tournament" onClick={() => history.push(props.match.url+"/addTournament")}/>
    </Paper>
  )
}

export default createFragmentContainer(withRouter(TournamentsInRounds), 
  {
    game: graphql`
      fragment TournamentsInRound_game on Game {
        _id
        customFields {
          label
          type
          options
        }
        metrics
      }
    `
  }
);