import React from 'react'
import Close from '@material-ui/icons/CloseRounded'
import { IconButton, Divider } from '@material-ui/core'
import { useHistory } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import TournamentBasic from './TournamentBasic'
import GameSpecific from './GameSpecific';
import Prizes from './Prizes'

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  backButton: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

function getSteps() {
  return ['Game Details', 'Prizes', 'Tournament details'];
}


function AddTournament(props) {

  const history = useHistory()

  return <div>
    <Divider />
    <p> <IconButton onClick={() => history.goBack()}><Close /></IconButton> Add Tournament </p>
    <HorizontalLabelPositionBelowStepper game={props.game}/>
  </div>
}

function HorizontalLabelPositionBelowStepper(props) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const [formData, setFormData] = React.useState({})
  const steps = getSteps();

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  function getData(metrics, customFields) {
    setFormData({...formData, metrics, customFields})
    handleNext()
  }

  function getTableData(data) {
    console.log(data)
    setFormData({...formData, prizes: data})
    handleNext()
  }

  function submitForm(data) {
    console.log(data, formData)
  }

  function getStepContent(stepIndex) {
    switch (stepIndex) {
      case 0:
        return <GameSpecific game={props.game} getData={getData}/>;
      case 1:
        return <Prizes getTableData={getTableData} handleBack={handleBack}/>;
      case 2:
        return <TournamentBasic game={props.game} handleBack={handleBack} submitForm={submitForm}/>;
      default:
        return 'Unknown stepIndex';
    }
  }

  return (
    <div className={classes.root}>
      <Stepper activeStep={activeStep} alternativeLabel>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      {getStepContent(activeStep)}
    </div>
  );
}


export default AddTournament;