import React from 'react'
import QueryRenderer from 'Components/QueryRenderer'
import graphql from 'babel-plugin-relay/macro'
import {withRouter} from 'react-router-dom'
import {Grid, TextField, Switch} from '@material-ui/core'
import Form from 'Components/Form'
import LoadingButton from 'Components/LoadingButton'
import CustomList from 'Components/CustomList'
import makeDate from 'Utils/MakeDate'
import {makeStyles} from '@material-ui/core/styles'
import GoBackAppBar from 'Components/GoBackAppbar'
import CommitRelay from 'Components/CommitRelay'
import { toastSuccess } from 'Components/Toast'

const styles = makeStyles(theme => ({
  container: {
    margin: 0,
    width: "100%"
  },
  addPadding: {
    '& > *' : {
      marginBottom: theme.spacing(1)
    }
  }
}))

const query = graphql`
  query DetailedReportQuery($id: ID!) {
    specificWithdraw(id: $id) {
      _id
      amount
      method
      createdAt
      approved
      userId{
        walletBalance
        bonus
        phone
        username
        transaction(pagination: {offset: 0, limit: 40}) {
          _id
          amount
          createdAt
          transactionType
          responseMessage
          responseCode
        }
      }
    }
  }
`

const mutation = graphql`
  mutation DetailedReportMutation($data: UpdateWithdrawInput!){
    updateWithdrawRequest(data: $data) {
      _id
      amount
      method
      createdAt
      approved
      userId{
        walletBalance
        bonus
        phone
        username
        transaction(pagination: {offset: 0, limit: 40}) {
          _id
          amount
          createdAt
          transactionType
          responseCode
          responseMessage
        }
      }
    }
  }
`

function DetailedReport(props) {
  const id = props.match.params.id
  //console.log(props)
  return (
    <div>
      <QueryRenderer query={query} variables={{id}} component={data => <DetailedReportComponent {...data} id={id}/>}/>
    </div>
  )
}

function makeList(data){
  return [
    {
      id: 1,
      primaryText: "Method",
      secondaryText: data.method
    },
    {
      id: 2,
      primaryText: "Requested on",
      secondaryText: makeDate(data.createdAt)
    },
    {
      id: 3,
      primaryText: "Requested Amount",
      secondaryText: "Rs."+data.amount
    },
    {
      id: 4,
      primaryText: "User wallet Balance",
      secondaryText: "Rs."+data.userId.walletBalance
    },
    {
      id: 5,
      primaryText: "User bonus balance",
      secondaryText: "Rs."+data.userId.bonus
    },
    {
      id: 6,
      primaryText: "Status",
      secondaryText: data.approved === 1 ? "Approved" : data.approved === 2 ? "Rejected" : "Pending"
    },
    {
      id: 7,
      primaryText: "Username",
      secondaryText: data.userId.username
    },
    {
      id: 8,
      primaryText: "Phone number",
      secondaryText: data.userId.phone
    }
  ]
}

function makeTransactionList(data) {
  let items = []
  let totalUsed = 0
  let totalCredited = 0
  //console.log(data)
  for(let i = 0; i < data.length;i++) {
    //console.log(data)
    if(data[i].transactionType === "USED"){
      totalUsed += data[i].amount
    }else if((data[i].transactionType === "CREDIT" || data[i].transactionType === "ADD" ) && +data[i].responseCode === 1){
      totalCredited += +data[i].amount
    }
    items.push({
      id: data[i]._id,
      primaryText: data[i].transactionType+" "+data[i].amount,
      secondaryText: "Response code:"+data[i].responseCode+", Message:"+data[i].responseMessage+", Date:"+makeDate(data[i].createdAt)
    })
  }
  items.unshift({
    id: 2,
    primaryText: "Total Credited",
    secondaryText: "Rs. "+totalCredited
  })
  items.unshift({
    id: 1,
    primaryText: "Total Used",
    secondaryText: "Rs. "+totalUsed
  })
  return items
  
}

const Approval = props => {


  let [approval, setApproval] = React.useState(false)
  let [loading, setLoading] = React.useState(false)
  
  function onSubmit(data){
    if(!window.confirm("Do you wish to submit")){
      return
    }
    setLoading(true)
    CommitRelay({data: {id: props.id, approval: approval ? 1 : 0, message: data.message}}, mutation, (resposne, error) => {
      setLoading(false)
      if(!error){
        toastSuccess("Transaction status has been updated")
      }
    })
  }

  return (<Form onSubmit={onSubmit}>
    <label>Approve request <Switch checked={approval} onChange={e => setApproval(!approval)} name="approval"/></label>
    <TextField label={'Message'} name="message" required fullWidth/>
    <LoadingButton type="submit" isLoading={false}> {approval ? 'Send money': "Reject"} </LoadingButton>
  </Form>)
}

function DetailedReportComponent(props) {
  const classes = styles()
  //console.log(props)
  let withdrawList = makeList(props.specificWithdraw)
  let transactionList = makeTransactionList(props.specificWithdraw.userId.transaction)

  return <Grid container spacing={1} className={classes.container}>
    <Grid item sm={6} xs={12} className={classes.addPadding}>
      <CustomList items={withdrawList} divider/>
      <Approval id={props.id}/>
    </Grid>
    <Grid item xs={12} sm={6}>
      <CustomList items={transactionList} />
    </Grid>
  </Grid>
}

export default withRouter(DetailedReport)