const PAGE = "ADD_TOURNAMENT::"

export const setGame = game => ({
  type: PAGE+"SET_GAME",
  game
})

export const setCustomFields = (name, value) => ({
  type: PAGE+"SET_CUSTOM_FIELDS",
  name,
  value
})

export const setPrizes = (data) => ({
  type: PAGE+"SET_PRIZES",
  data
})

export const resetData = () => ({
  type: PAGE+"RESET_DATA"
})

export const addGameSpecificData = (data) => ({
  type: PAGE+"ADD_GAME_DATA",
  data
})  

export const setMetrics = metrics => ({
  type: PAGE+"SET_METRICS",
  metrics
})