import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import {Grid} from '@material-ui/core'
import { createFragmentContainer } from 'react-relay'
import GameCard from './GameCard'
import CommitMutation from 'Components/CommitRelay'
import DeleteQuery from './DeleteGame'
import {useHistory} from 'react-router-dom'
import {toastSuccess, toastError} from 'Components/Toast'
import Fab from 'Components/Fab'

function GamesList(props){
  
  let history = useHistory()

  function deleteItem(id) {
    if(!window.confirm("Do you wish ti delete this game")){
      return
    }
    CommitMutation({id}, DeleteQuery, (response, errors) => {
      if(!response) {
        return toastError("Unable to delete game")
      }
      if(!errors){
        toastSuccess("Removed game")
      }
    },
    {
      updater: store => {
        store.delete(id)
      }
    })
  }
  return (
    <React.Fragment>
      <Grid container>
        {props.games.map((game, index) => <GameCard key={index}  game={game} deleteItem={deleteItem}/>)}
      </Grid>
      <Fab onClick={() => history.push("/games/add")} />
    </React.Fragment>
  )
}


export default createFragmentContainer(GamesList, {
  games: graphql`
    fragment GamesList_games on Game @relay(plural: true) {
      ...GameCard_game
    }
  `
})