import React from 'react'
import QueryRenderer from 'Components/QueryRenderer'
import graphql from 'babel-plugin-relay/macro'
import Grid from '@material-ui/core/Grid'
import {Route, withRouter} from 'react-router-dom'
import BasicDetails from './BasicDetails'
import {makeStyles} from '@material-ui/core/styles'
import SpeedDial from './SpeedDial'
import AddRound from './Rounds/AddRound'
import RoundsOverview from './Rounds/RoundsOverview'
import TournamentsInRounds from './Rounds/TournamentsInRound'
import Error from 'Components/Error'

const styles = makeStyles(theme => ({
  container: {
    margin: 0,
    width: "100%"
  }
}))

const query = graphql`
  query UpdateMegaQuery($id: ID!) {
    megladron(filter: {_id: $id}) {
      docs {
        ...BasicDetails_megladron
        ...RoundsOverview_rounds
        game {
          ...TournamentsInRound_game
        }
        createdBy {
          username
        }
      }
    }
  }
`

function UpdateMega(props) {
  console.log(props)
  return (
    <QueryRenderer query={query} variables={{id: props.match.params.id}} component={data => <UpdateMegaComponent {...data} />}/>
  )
}

function UpdateMegaComponent(props) {
  //because id based search so only one document would be returned
  const classes = styles()
  let megladron = null
  try {
    megladron = props.megladron.docs[0]
  }catch(err) {
    return <Error />
  }
  //console.log(megladron)
  if(!megladron) {
    return <Error />
  }
  return (
    <Grid container spacing={1} className={classes.container}>
      <Grid item xs={12} sm={4}>
        <BasicDetails megladron={megladron}/>
      </Grid>
      <Grid item xs={12} sm={8}>
        <Route path={'/mega/view/:id/addRound'} component={AddRound}/>
        <RoundsOverview rounds={megladron} game={megladron.game}/>
      </Grid>
      <SpeedDial />
    </Grid>
  )
}

export default withRouter(UpdateMega)