import React from 'react'
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import Button from 'Components/LoadingButton'
import * as yup from 'yup'
import {TextField} from '@material-ui/core'
import {DateTimePicker} from '@material-ui/pickers'
import {makeStyles} from '@material-ui/core/styles'

const styles = makeStyles(theme => ({
  form: {
    '& > *': {
      marginBottom: theme.spacing(1)
    }
  }
}))

const schema = yup.object().shape({
  title: yup.string().required().max(30),
  maxPlayers: yup.number().integer().required(),
  maxGroups: yup.number().integer().required(),
  entryFee: yup.number().integer().required(),
  description: yup.string().required()
})

const InputField = props => (
  <TextField fullWidth error={props.errors[props.name] !== undefined} helperText={props.errors[props.name]?.message} {...props}/>
)

function TournamentBasic(props) {
  const {register, errors, handleSubmit} = useForm({
    resolver: yupResolver(schema)
  })

  const classes = styles()
  let [time, setTime] = React.useState(Date.now() + ( 1000 * 60 * 60 * 24))

  function submitForm(data) {
    props.submitForm({...data, time})
  }

  return <form onSubmit={handleSubmit(submitForm)} className={classes.form}>
    <InputField errors={errors} label={'Tournament title'} name="title" fullWidth required inputRef={register} />
    <DateTimePicker name="time" onChange={t => setTime(t)} required value={time} label={"Tournament time"} fullWidth disablePast/>
    <InputField inputRef={register} errors={errors} label={'Entry Fee'}  fullWidth name="entryFee" type="number" required/>
    <InputField inputRef={register} errors={errors} type="number" name="maxPlayers" label={'Max Participants'} fullWidth type="number" required/>
    <InputField inputRef={register} errors={errors} name="image" fullWidth label={"Image URL"} defaultValue={props.game.image}/>
    <InputField inputRef={register} errors={errors} type="number" name="maxGroups" fullWidth label={"Max groups that could be formed"} helperText={"If no grouping enter same number as max players"} required/>
    <InputField inputRef={register} errors={errors} label={'Tournament Description'} fullWidth multiline rows={5} name="description" helperText={"Add things like rules or anything specific"} required/>    
    <Button isLoading={false} type="submit">Create Tournament</Button>
  </form>
}

export default TournamentBasic